import React from 'react';
import Buttom from '../button/Button.js'
import { useEffect, useState } from 'react';
import "./NavBar.css"



function NavBar(){
  const [scroll, setScroll] = useState(window.scrollY);

  
  useEffect(() => {
    const navbar = document.getElementsByTagName("nav")[0];

    const handleScroll = () => {
      const currentScrollPos = window.scrollY;

      if (scroll < currentScrollPos) {
        navbar.style.top = "-50px";
      } else {
        navbar.style.top = "0";
      }
      setScroll(currentScrollPos);
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [scroll]);



    return (
        <nav className="navBar">
            <ul>
                <li>
                    <Buttom path="#cards" text="Selection Card"/>
                </li>
                <li>
                    <Buttom path="#tricks" text="Tricks"/>
                </li>
                <li>
                    <Buttom path="#win" text="Win"/>
                </li>

                <li>
                    <Buttom path="https://www.moxfield.com/decks/v00Bw4xtZUGlqu60IcdKXw" text="MoxField"/>
                </li>


            </ul>
        </nav>

    )
}


function showBar(){// Obtenha a barra de navegao
    const navbar = document.getElementsByTagName("nav")[0];
    navbar.style.position="fixed"
    // Obtenha a posio atual de rolagem vertical da janela
    let prevScrollPos = window.scrollY;
    
    // Defina um evento de rolagem para a janela
    window.onscroll = function() {
        // Obtenha a posio atual de rolagem vertical da janela
        const currentScrollPos = window.scrollY;    
    
        // Verifique se a posio atual  maior que a anterior (rolagem para baixo)
        if (prevScrollPos < currentScrollPos) {
            // Esconda a barra de navegao movendo-a acima do topo da janela
            navbar.style.top = "-50px";
        } else {
            // Exiba a barra de navegao movendo-a de volta para a posio normal
            navbar.style.top = "0";
        }
    
        // Atualize a posio anterior de rolagem
        prevScrollPos = currentScrollPos;
    }
}


export default NavBar